/* 17.a Post Feed
/* ---------------------------------------------------------- */


.post-feed {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    padding: 40px 0 0 0;
}

.posts-inner {
    max-width: none;
}

.post-card {
    flex: 1 1 300px;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    margin: 0 20px 40px;
    min-height: 300px;
    background: #fff center center;
    background-size: cover;
    border-radius: 5px;
    box-shadow: rgba(39,44,49,0.06) 8px 14px 38px, rgba(39, 44, 49, 0.03) 1px 3px 8px;
