.post-card:hover {
    box-shadow: 0 0 1px rgba(39,44,49,0.10), 0 3px 16px rgba(39, 44, 49,0.07);
    transition: all 0.3s ease;
    transform: translate3D(0, -1px, 0);
    text-decoration: none;
}

.post-card a, .post-card a:hover {
    text-decoration: none;
    color: inherit;
}

.post-card-image-link {
    position: relative;
    display: block;
    overflow: hidden;
    border-radius: 5px 5px 0 0;
}

.post-card-image {
    width: auto;
    height: 200px;
    background: lightgrey no-repeat center center;
    background-size: cover;
}

.post-card-content-link {
    position: relative;
    display: block;
    padding: 25px 25px 0;
    color: darkgrey;
}

.post-card-content-link:hover {
    text-decoration: none;
}

.post-card-tags {
    display: block;
    margin-bottom: 4px;
    color: grey;
    font-size: 1.5rem;
    line-height: 1.15em;
    font-weight: 500;
    letter-spacing: 0.5px;
}

.post-card-title {
    margin-top: 0;
    font-size: 2.6rem;
}

.post-card-content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;    
}

.post-card-excerpt {
    text-align: left;
}

.post-card-excerpt p {
    font-size: 1.5rem;   
    line-height: 1.7em;
}

.post-card-meta {
    padding: 0 25px 25px;
}

.author-profile-image {
    margin-right: 5px;
    width: 25px;
    height: 25px;
    border-radius: 100%;

    object-fit: cover;
}

.post-card-author {
    font-size: 1.6rem;
    font-weight: 500;
